h1.Timer-wrapper {
    display: inline-block;
    padding: 2px;
    border: 1px solid red;
    width: 100px;
    display: none;
   }
div.title {
    border-style: dashed;
    border-width: 2px;
    border-color: whitesmoke;
    background-color: rgba(0, 0, 0, 0.7);
    height: 300px;
    width: 500px;
    text-align: center;
    position: relative;
    margin: auto;
}

.titleWords {
    color: greenyellow;
}

.titleButton {
    
}
.Grid {
  display: flex;
  flex-direction: column;
}

tr {
  border: 1px solid #0034f2;
}

table {
  border: 3px solid #141c3a;
  border-collapse: collapse;
  margin: auto;
}

td {
  border: 1px solid #141c3a;
  width: 30px;
  height: 30px;
  text-align: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-weight : bold;

}

.right-border {
  border-right: 3px solid #141c3a;
}

.bottom-border {
  border-bottom: 3px solid #141c3a;
}

.cell-modifiable{
  cursor: pointer;
  color: blue;
}

.cell-invalid {
  background-color: #7a1b1bb6;
}
.choice-container {
  display: flex;
  justify-content: center;
}

.choice {
  padding: 1px 5px;
  font-weight: 900;
  border: 2px solid;
  margin: 2px;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
}

.selected {
  background-color: #141c3a;
}
.selected-eraser {
  background: lightblue;
}

.selected p {
  color: white;
}

.noSelect {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.noSelect:focus {
    outline: none !important;
}

